<template>
  <div v-if="footerStatus" id="footer" class="footer" :class="{footerAbsolute: $store.state.footerAbsolute}">
    <!-- 以 / 开头的路由是根路由 -->
    <router-link :click="getUrl()" :to="{path: '/' + tab.sign}" v-for="(tab, index) in pfooter" class="tabs" :key="index">
      <img :src="getSign(tab.sign)" :alt="tab.title" :title="tab.title">
      <p :class="{active: tab.sign === currentRoute}">{{ tab.title }}</p>
    </router-link>
  </div>
</template>
<script>
// import { mapMutations } from 'vuex'
export default {
  props: ['footerStatus'],
  data () {
    return {
      pfooter: [
        { title: '首页', sign: 'home' },
        { title: '分类', sign: 'category' },
        { title: '购物车', sign: 'shopping' },
        { title: '我的', sign: 'user' }
      ],
      currentRoute: ''
    }
  },
  mounted () {
    this.getRoute()
    console.log(this.currentRoute)
  },
  methods: {
    getSign (sign) {
      sign = (sign === this.currentRoute) ? sign + '_active' : sign
      return '../../static/images/footer/' + sign + '.png'
    },
    getRoute () {
      this.currentRoute = this.$route.path.replace('/', '')
    },
    getUrl () {
      this.getRoute()
    }
  }
}
</script>
<style scoped>
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #dfdfdf;
  overflow: hidden;
  background-color: #ffffff;
}
.footer.footerAbsolute{
  position: absolute;
}
.footer .tabs {
  display: block;
  color: #262a40;
  width: 25%;
  height: 1.2rem;
  padding-bottom: .15rem;  
  text-decoration: none;
  float: left;
}
.footer .tabs img{
  width: .6rem;
  height: .6rem;
  vertical-align: top;
}
.footer .tabs p{
  font-size: .35rem;
  height: .35rem;
  line-height: .35rem;
  text-align: center;
  position: relative;
  top: -.4rem;
}
.footer .tabs p.active{
  color: #e21323;
}
</style>



