/* eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'

import Register from '@/components/main/register/Register'
import Login from '@/components/main/login/Login'
import Home from '@/components/main/home/Home'
import Category from '@/components/main/category/Category'
import Shopping from '@/components/main/shopping/Shopping'
import User from '@/components/main/user/User'
import Record from '@/components/main/record/Record'

Vue.use(Router)
export default new Router({
  routes: [{
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/category',
      name: 'Category',
      component: Category
    },
    {
      path: '/shopping',
      name: 'Shopping',
      component: Shopping
    },
    {
      path: '/user',
      name: 'User',
      component: User,
      children: [{
        path: '/record',
        component: Record
      }]
    }
  ]
})



