<template>
<div class="loginbg">
  <div class="container">
    <div class="user item">
      <img src="./images/user.png" alt="user">
      <input type="text" placeholder="请输入用户名">
    </div>
    <div class="password item">
      <img src="./images/password.png" alt="password">
      <input type="password" placeholder="请输入密码">
    </div>
    <div class="code_check item">
      <img src="./images/key.png" alt="key">
      <input type="text" placeholder="请输入验证码">
    </div>
    <div class="passwordabout">
      <p class="forgets">忘记密码?</p>
      <p class="remem">记住密码</p>
    </div>
    <div class="login-register">
      <button class="login">
        <router-link to="/user">登录</router-link>
      </button>
      <button class="register">
        <router-link to="/register">免费注册</router-link>
      </button>
    </div>
  </div>
  <p>登录即代表阅读并同意<span @click="getServices">服务条款</span></p>
  <div @click="getServices" v-if="servicesStatus" class="mask"></div>
  <div id="services">
    <Service :servicesStatus="servicesStatus"></Service>
  </div>
</div>
</template>
<script>
import Service from './Service'
export default {
  data () {
    return {
      servicesStatus: false
    }
  },
  methods: {
    getServices () {
      this.servicesStatus = !this.servicesStatus
    }
  },
  components: {
    Service
  }
}
</script>
<style scoped>
.loginbg{
  min-height: 667px;
  background-image: url("./images/bg.png");
  background-size: 100% 100%;
}
.container{
  padding-top: 5rem;
}
.item{
  margin: 0 auto;
  height: 1rem;
  width: 80%;
  margin-bottom: .5rem;
  overflow: hidden;
  border-radius: 3px;
  background-color: rgba(0,0,0,.3);
}

.item img,
.item input{
  float: left;
  height: .8rem;
  margin: .1rem 0;
}
.item img{
  height: .6rem;
  margin-top: .2rem;
  margin-left: .2rem;
}
.item input {
  color: #dddddd;
  font-size: .4rem;
  border: none;
  outline: none;
  margin-left: .2rem;
  padding-left: .2rem;
  background-color: transparent;
  border-left: 1px solid #aaaaaa;
}
/* passwordabout */
.passwordabout{
  width: 80%;
  margin: 0 auto;
  overflow: hidden;
  /* border: 1px solid red; */
}
.passwordabout p{
  color: #ffffff;
  font-size: .4rem;
}
.passwordabout .forgets{
  float: left;
}
.passwordabout .remem{
  float: right;
}
/* login register */
.login-register{
  width: 80%;
  margin: 2rem auto 0;
  overflow: hidden;
}
.login-register button{
  width: 48%;
  height: 1.2rem;
  border: none;
  outline: none;
  border-radius: 3px;
  color: #ffffff;
  font-size: .4rem;
}
.login-register .login{
  float: left;
  background-color: #392b42;
}
.login-register .register{
  float: right;
  background-color: #3d3157;
  box-shadow: 0 0 0 3px #2f2644 inset;
}
.login-register button a{
  display: block;
  width: 100%;
  height: 100%;
  line-height: 1.2rem;
  color: #ffffff;
  text-decoration: none;
}
.loginbg > p{
  padding: 1rem 0;
  color: #aaaaaa;
  font-size: .35rem;
}
.loginbg > p > span{
  color: #ffffff;
  font-size: .4rem;
}
.mask{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0,0,0,.3);
}
/* placeholder 颜色修改 */
::-webkit-input-placeholder {
  color:#aaaaaa;
}
:-moz-placeholder {
  color:#aaaaaa;
}

::-moz-placeholder {
  color:#aaaaaa;
}
:-ms-input-placeholder {
  color:#aaaaaa;
}
</style>



