<template>
  <div class="home app">
    <div id="search_area">
      <input @focus="positionFooter()" id="search" class="search" type="search" placeholder="搜索您想要的商品" />
      <input class="ok" type="button" value="搜索" />
    </div>
    <swipe class="swipe">
      <swipe-item class="slide">
        <img src="../../../../static/images/main.jpg" alt="">
      </swipe-item>
      <swipe-item class="slide">
        <img src="../../../../static/images/main.jpg" alt="">
      </swipe-item>
      <swipe-item class="slide">
        <img src="../../../../static/images/main.jpg" alt="">
      </swipe-item>
      <swipe-item class="slide">
        <img src="../../../../static/images/main.jpg" alt="">
      </swipe-item>
      <swipe-item class="slide">
        <img src="../../../../static/images/main.jpg" alt="">
      </swipe-item>
    </swipe>
    <div class="items">
      <div v-for="(item, index) in items" class="item" :key="index">
        <img :src="item.src" :alt="item.title">
        <p>{{ item.title }}</p>
      </div>
    </div>
    <p class="congra">热烈祝贺拼拼购商城成功上线</p>
    <div class="goodsall">
      <Goodsingle v-for="n in 10" :goodsingle="goodsingle" :key="n"></Goodsingle>
    </div>
  </div>
</template>

<script>
import { Swipe, SwipeItem } from 'vue-swipe'
import Goodsingle from '../../common/goodsingle'
// import axios from 'axios'
import { mapMutations, mapActions } from 'vuex'
export default {
  name: 'Home',
  data () {
    return {
      items: [
        {src: '../../../../static/images/items/ppg.png', title: '拼拼购'},
        {src: '../../../../static/images/items/nhzq.png', title: '年货专区'},
        {src: '../../../../static/images/items/jfzq.png', title: '积分专区'},
        {src: '../../../../static/images/items/qbfl.png', title: '全部分类'}
      ],
      goodsingle: {
        desc: 'asda',
        money: 'asda'
      }
    }
  },
  mounted () {
    this.init()
  },
  components: {
    Swipe,
    SwipeItem,
    Goodsingle
  },
  methods: {
    init () {
      // let that = this
      // let url = 'http://127.0.0.1:8080/YlejiaApp/wxIndex/getCafeteriaJson'
      // axios.get(url).then(function (responese) {
      //   console.log(responese)
      //   console.log(that)
      // })
    },
    ...mapMutations(['positionFooter']),
    ...mapActions(['addActions', 'reduceActions'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* 引入 vue-swipe 的样式表 */
@import "../../../../static/css/vue-swipe.css";
#search_area{
  width: 90%;
  height: 1rem;
  margin: .2rem auto;
  overflow: hidden;
}
.search{
  width: 80%;
  text-align: center;
  border: 1px solid #afafaf;
  border-right: none;
  border-top-left-radius: .5rem;
  border-bottom-left-radius: .5rem;
}
.search:focus{
  border: 1px solid #3385ff;
  border-right: none;
}

.ok{
  width: 20%;
  border: none;
  color: #ffffff;
  border-top-right-radius: .5rem;
  border-bottom-right-radius: .5rem;
  background-color: #3385ff;
  border: 1px solid #3385ff;
}
#search_area input{
  font-size: .4rem;
  height: inherit;
  outline: none;
  float: left;  
}
/* 轮播 */
.swipe{
  width: 100%;
  height: 4.8rem;
  overflow: hidden;
}
.slide{
  width: 100%;
  background-color: rgb(17, 116, 116);
}
.slide img{
  width: 100%;
}
.items{
  width: 100%;
  overflow: hidden;
  margin-top: .2rem;
}
.item{
  width: 25%;
  float: left;
}
.item img{
  width: 50%;
}
.item p{
  font-size: .4rem;
}
.congra{
  text-align: left;
  margin: .3rem 0 .3rem .5rem;
  font-size: .3rem;
}
.goodsall{
  overflow: hidden;
}
</style>



