<template>
  <div class="goodsingle">
    <img src="../../../static/images/goods/goods_single.gif" alt="">
    <p class="desc">{{ goodsingle.desc }}</p>
    <p class="money">${{ goodsingle.money }}</p>
  </div>
</template>
<script>
export default {
  props: ['goodsingle'],
  data () {
    return {
    }
  }
}
</script>
<style>
.goodsingle{
  float: left;
  width: 46%;
  margin-left: 2%;
  margin-right: 2%;
  border: 1px solid #dfdfdf;
}
.goodsingle img{
  width: 100%;
}
.goodsingle p{
  text-align: left;
  font-size: .3rem;
}
.money{
  color: tomato;
}
</style>



