// 更改 store 的各种同步的状态
// 每一个 mutations 接受 state 作为第一个参数
export const mutations = {
  add (state, payload) {
    state.count *= payload.amount
  },
  reduce (state, payload) {
    state.count /= payload.amount
  },
  positionFooter (state) {
    state.footerAbsolute = true
  }
}



