<template>
  <div class="user app">
    <!-- 头部 注册 登录 -->
    <header>
      <img src="./images/default-avatar.png" alt="用户的头像">
      <h3>
        <router-link to="/login">
          登录 - 注册
        </router-link>
      </h3>
    </header>
    <div class="links">
      <router-link class="link" to="/user/record" tag="div">
        <img src="./images/record.png" alt="拼团记录">
        <p>拼团记录</p>
      </router-link>
      <router-link class="link" to="/user/record" tag="div">
        <img src="./images/account_balance.png" alt="拼团记录">
        <p>账户余额</p>
      </router-link>
      <router-link class="link" to="/user/record" tag="div">
        <img src="./images/get_balance.png" alt="拼团记录">
        <p>提成金额</p>
      </router-link>
    </div>
    <div class="items">
      <Item v-for="n in 30" :key="n"></Item>
    </div>
  </div>
</template>

<script>
import Item from './Item'
export default {
  name: 'User',
  data () {
    return {
    }
  },
  components: {
    Item
  }
}
</script>
<style scoped>
/* header */
header{
  height: 3rem;
  padding: .5rem;
  margin-bottom: .2rem;
  overflow: hidden;
  background-color: #ff6363;
}
header img{
  float: left;
  width: 2rem;
  height: 2rem;
}
header h3{
  font-size: .6rem;
  float: left;
  height: 2rem;
  padding-left: 1rem;
  line-height: 2rem;
  vertical-align: top;
}
header h3 a{
  display: block;
  color: #ffffff;
  height: 100%;
  text-decoration: none;
}
/* links */
.links{
  overflow: hidden;
  margin-bottom: .5rem;
}
.links .link{
  width: 33.333%;
  float: left;
}
.links .link:nth-child(2){
  border-left: 1px solid #8888;
  border-right: 1px solid #8888;
}
.links .link img{
  width: .8rem;
  height: .8rem;
}
.links .link p{
  font-size: .4rem;
  font-weight: 600;
}
</style>



