<template>
  <div id="app">
    <router-view/>
    <Pfooter :footerStatus="getStatus"></Pfooter>
  </div>
</template>

<script>
import Pfooter from './components/common/footer'
export default {
  name: 'app',
  data () {
    return {
      getStatus: false
    }
  },
  components: {
    Pfooter
  },
  watch: {
    // 侦测路由变化
    $route () {
      this.routeInit()
    }
  },
  mounted () {
    this.routeInit()
  },
  methods: {
    routeInit () {
      this.getStatus = /Home|Category|Shopping|User/gi.test(this.$route.name)
    }
  }
}
</script>

<style>
*{
  margin: 0;
  box-sizing: border-box;
}
#app {
  position: relative;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



