    <h1>拼拼购平台用户服务协议</h1>
    <p>
      本《拼拼购平台用户服务协议》协议(以下简称“本协议”)是由您(以下称为“用户”)就拼拼购平台上所提供的产品和服务所订立的协议。
    </p>
