      <li>拼拼购平台在提供产品和服务时，可能部分产品服务需要向用户收取一定的虚拟物品。在此情况下，相关页面上会有明确的提示。如用户不同意使用该部分虚拟物品，可选择不接受相应的产品和服务。</li>
      <li>拼拼购平台通过服务器端设备接入互联网为用户提供产品和服务。</li>
    </ol>
