        </strong>
      </li>
      <li>用户发现其用户账号或密码被他人非法使用或有使用异常情况的，应及时通过拼拼购平台联系官方客服并配合平台工作人员完成用户身份核对和验证账号所有权，确定账号所有权后客户有权请求拼拼购平台采取措施暂停该账号的登录和使用。</li>
    </ol>
