          <li>用户直接购买商城商品的不予累积积分；违反拼拼购平台相关规定的不予累积积分。</li>
          <li>用户账户内积分只能在拼拼购平台规定范围内使用，不可出售、转让和赠送至其他用户或任何第三人。</li>
          <li>拼拼购用户办理注销账户时，如注销账户内仍有积分，则用户积分将在注销账户时自动清零。</li>
          <li>我司将不定期开展其他各类奖励积分活动，具体以活动公布的规则为准。若用户在拼单或消费同时满足两项及以上奖励活动条件，按最高奖励数累计积分。同时，在用户普通的单笔及单月积分累计上限，不因奖励积分活动而改变。</li>
          <li>若出现用户自身原因造成操作失误或违反拼拼购平台交易规定，因此影响积分累计的，我司不承担责任。</li>
        </ol>
      </li>
      <li>积分有效期
        <p>2018年1月1日起，拼拼购平台所有注册用户积分将设置有效期，积分累积周期为每半年清零，到期后积分做清零处理。如：2018年1月1日（含）前累积的积分，将于2018年6月30日到期，积分到期后所有累积积分系统将全部自动清零。</p>
      </li>
      <li>积分兑换
        <ol>
          <li>用户可将个人账户积分依照拼拼购积分兑换标准和兑换规则，兑换相应的商城商品，积分兑换申请仅限用户本人申请。</li>
          <li>积分消费及其他各积分兑换项目的具体兑换规则，均以兑换当时最新积分活动公告或目录为准。</li>
          <li>用户在申请兑换时，视为本人同意将通讯、邮寄等个人资料用于商品供应商配送处理。</li>
        </ol>
      </li>
      <li>积分兑换方法
        <ol>
          <li>商城内部分商品可以直接用积分兑换，用户可以选择完全使用积分兑换。</li>
          <li>商城内部分商品只能使用部分积分兑换，用户需要同时使用积分和货币进行兑换。</li>
        </ol>
      </li>

    </ol>
