    <h2>八、资费政策</h2>
    <p>
      对于拼拼购平台的收费产品和服务，用户应该按照拼拼购平台确定的资费政策购买拼拼购平台的产品和服务。如果用户未按拼拼购平台确定的资费政策购买拼拼购平台的产品和服务，拼拼购平台可以立即停止向用户提供该产品和服务。
      <strong class="red">
        除非法律另有明文规定，否则用户不得要求拼拼购平台返还用户已经支付予拼拼购平台的任何资费(以下简称“退款”)，无论该等资费是否已被消费。拼拼购平台有权决定是否、何时、以何种方式向用户退款。拼拼购平台同意退款的，用户应补偿支付时使用信用卡、手机等支付渠道产生的费用，拼拼购平台有权在返还用户的资费中直接扣收。对于用户存在违反本协议的行为的，拼拼购平台有权不予退款。拼拼购平台在产品和服务提供过程中赠送的充值金额、虚拟货币、虚拟道具等，不予退款或变现。
      </strong>
    </p>
    <h2>九、虚拟物品</h2>
    <ol>
      <li>
        由于不可抗力或用户个人操作问题造成的角色被删或回档、虚拟物品或游戏道具的损失，
        <strong class="red">由用户自行承担责任，拼拼购平台不予任何赔偿或补偿。</strong>
        （不可抗力的详细解释参见本协议第十一、十二条）。
      </li>
      <li>
        <strong class="red">
          鉴于网络虚拟物品交易的复杂性和安全性，拼拼购平台禁止用户进行游戏虚拟物品买卖线下交易及线下交易相关行为。
        </strong>
      </li>
      <li>
        拼拼购平台不认可用户线下交易所产生的交易结果，用户通过线下交易所获得的游戏虚拟物品将被认定为来源不符合游戏规则。拼拼购平台有权按照本协议第六条的约定，对线下交易及线下交易相关行为涉及的游戏虚拟物品、游戏角色与用户账号采取相应的措施。
      </li>
    </ol>
    <h2>十、服务方式、内容的变动与个人资料转移</h2>
    <ol>
      <li>
        <strong class="red">
          拼拼购平台将尽力持续地向用户提供产品和服务，但是拼拼购平台并不排除拼拼购平台可能会停止提供任何产品和服务的可能性，也不排除任何改变游戏服务或其他网络服务的服务方式、服务内容的可能性。
        </strong>
      </li>
      <li>
        为增加并丰富拼拼购平台的产品和服务及其他网络服务的内容，所有产品和服务及拼拼购平台在运行时可能不定期更新并调整其包含的功能。在对应产品和服务及拼拼购平台更新后，一切的操作、内容、设定，将以对应产品或拼拼购平台中的公告内容为准。
      </li>
      <li>
        <strong class="red">
          如果拼拼购平台停止提供某一项产品和服务，或改变某一项产品和服务的方式或内容，拼拼购平台将会提前三日以公告或站内信息通知用户，并尽力寻找适当的服务提供者以接替拼拼购平台继续提供产品和服务。
        </strong>
      </li>
      <li>
        在本条第3款所述情况下，拼拼购平台可能会将用户的个人资料(包括有关的账号和密码信息及个人资料)转移给该继续提供服务的一方，也可能根据需要，永久封存原有资料以推行新的游戏方式。用户在此同意拼拼购平台有权做此转移和提供，并且同意在拼拼购平台完成转移和提供之后，拼拼购平台将不再对用户原有资料承担任何义务和责任。但是，拼拼购平台并不保证拼拼购平台届时一定能够找到适当的服务提供者或服务方式以代替拼拼购平台继续提供产品和服务，也不保证拼拼购平台找到的服务提供者所提供的产品和服务或者改变的游戏方式能够满足用户的要求。
      </li>
    </ol>
    <h2>十一、服务中断或终止</h2>
    <ol>
      <li>
        如发生下列任何一种情形，拼拼购平台有权随时中断或终止向用户提供本协议项下的产品、服务和其他网络服务，对于因而产生的不便和损失，拼拼购平台不承担任何责任：
        <ol>
          <li>用户提供的个人资料不真实;</li>
          <li>用户违反本协议中规定的用户行为准则。</li>
        </ol>
      </li>
      <li>
        在下列情况下，拼拼购平台有权注销用户创建的账号、游戏角色，并删除该账号或游戏角色的所有记录：
        <ol>
          <li>用户或用户注册的游戏角色严重违反平台用户行为准则或对应游戏规则</li>
          <li>用户自行删除的角色或主动申请清除账号及所有记录;</li>
          <li>用户在注册账号第一个月时间内或连续6个月时间内，没有使用过此账号的(包括使用此账号登录或充值)。</li>
        </ol>
      </li>
      <li>
        关于拼拼购平台所提供的不同产品和服务注销角色的具体条件，请参见各个产品和服务的具体规定。
      </li>
      <li>
        为了保障拼拼购平台、单个产品和服务、游戏及游戏平台网站和服务器的正常运行，拼拼购平台需要定期或不定期对服务器进行停机维护，或针对突发事件进行紧急停机维护。因上述情况而造成的正常服务中断、中止，用户予以理解并同意，拼拼购平台则有义务尽力避免服务中断并将中断时间限制在最短时间内。
      </li>
      <li>
        <strong class="red">
          发生下列情形之一时，拼拼购平台有权不经提前通知，终止或中断服务器所提供之全部或部分服务，对因此而产生的不便或损失，拼拼购平台对用户或第三人均不承担任何责任
        </strong>
        <ol>
          <li>定期检查或施工，更新软硬件等;</li>
          <li>服务器遭受损害，无法正常运作;</li>
          <li>突发性的软硬件设备与电子通信设备故障</li>
          <li>网络提供商线路或其他故障</li>
          <li>在紧急情况之下依照法律的规定或为用户及第三者之人身安全;</li>
          <li>第三方原因或其他不可抗力的情形。</li>
        </ol>
      </li>
      <li>
        不管产品和服务由于任何原因终止，用户应采取相应的措施自行处理游戏及游戏平台上的虚拟物品。用户不得因终止服务而要求拼拼购平台承担用户已经购买但尚未使用的游戏虚拟货币及以外任何形式的赔偿或补偿责任，包括但不限于因不再能继续使用商城账号、游戏账号、游戏内虚拟物品等而要求的赔偿。
      </li>
    </ol>
    <h2>十二、有限保证及免责声明</h2>
    <ol>
      <li>
        用户若是通过拼拼购平台代理商所完成的开户，用户需遵守本协议规定，若有其他争议或纠纷，均与拼拼购平台无关，拼拼购平台不承担任何责任，用户自行与平台代理商协商解决。
      </li>
      <li>
        如有系统故障、安全漏洞(Security Vulnerability)、程序缺陷(Bug)、程序出错等问题，拼拼购平台有权把商城、游戏等的资料还原到一定日期，以维护游戏之平衡。用户不得因此要求补偿或赔偿。
      </li>
      <li>
        在适用法律允许的最大范围内，拼拼购平台对用户因使用拼拼购平台的产品和服务引起的任何间接、偶然、意外、特殊或继起的损害(包括但不限于人身伤害、隐私泄漏、因未能履行包括诚信或合理谨慎在内的任何责任、因过失和因任何其他金钱上的损失或其他损失而造成的损害赔偿)不负任何赔偿责任。
      </li>
      <li>
        拼拼购平台对本协议项下涉及的境内外基础电信运营商的固定及移动通信网络的故障，各类技术缺陷、覆盖范围限制、不可抗力、计算机病毒、黑客攻击、用户所在位置、用户关机、合作方因素、他人故意或过失行为或其他非拼拼购平台技术能力范围内的原因造成的服务中断、用户发送的数据或短信息的内容丢失、出现乱码、错误接收、无法接收、迟延接收等等，均不承担责任。
      </li>
    </ol>
    <h2>十三、知识产权及信息所有权</h2>
    <p>
      拼拼购平台通过产品和服务提供的商城平台、游戏软件(包括具备客户端软件及不具备客户端软件的游戏)、其他软件、信息、作品及资料，其著作权、专利权、商标专用权及其它知识产权，均为拼拼购平台或其相应权利人所有。
      <strong class="red">
        除非事先经拼拼购平台书面合法授权，或法律另有明文规定，任何人不得擅自以任何形式使用、复制、传播、伪造、模仿、修改、改编、翻译、汇编、出版、进行反编译或反汇编等反向工程，否则拼拼购平台有权立即终止向用户提供产品和服务，并依法追究其知识产权侵权责任，同时要求赔偿拼拼购平台由此造成的一切损失。
      </strong>
    </p>
    <h2>十四、损害赔偿</h2>
    <p>
      <strong class="red">
        用户若违反本协议或可适用的法律法规，导致拼拼购平台的母公司、子公司、其他关联公司、附属机构及其人员，受雇人、代理人及其他一切相关履行辅助人员因此受到损害或支出任何衍生费用(如诉讼费、律师费等诉讼相关费用)，用户应承担补偿相关费用及支付损害赔偿的责任。
      </strong>
    </p>
    <h2>
      十五、协议的终止
    </h2>
    <p>
      <strong class="red">
        用户应遵守本协议及有关法律法规的规定。拼拼购平台有权判断用户是否违反本协议，并有权据此终止该协议。拼拼购平台终止用户协议的，将提前三日以公告或站内信息通知该用户。
      </strong>
    </p>
    <h2>十六、修改和解释权</h2>
    <p>
      拼拼购平台保留对本协议条款、产品和服务以及拼拼购平台所提供的产品和服务的相关官方网站的最终解释权。
      <strong class="red">
        如果用户不同意本协议条款，或对拼拼购平台后来修改、更新的条款有异议，或对拼拼购平台所提供的产品和服务不满意，用户可以随时选择停止使用拼拼购平台的产品和服务。如果用户选择停止使用拼拼购平台的产品和服务，拼拼购平台不再对用户承担任何义务和责任。
      </strong>
    </p>
    <h2>十七、广告与外部链接</h2>
    <p>拼拼购平台的产品和服务中可能包含他人的商业广告或其它活动促销的广告。这些内容由广告商或商品/服务提供者提供并承担相应责任，拼拼购平台仅提供刊登内容的媒介。用户通过拼拼购平台或拼拼购平台所链接的网站所购买的该等服务或商品，其交易行为仅存于用户与该等商品或服务的提供者之间，与拼拼购平台无关，拼拼购平台不就用户与该商品或服务的提供者之间所产生的交易行为承担任何法律责任。</p>
    <h2>十八、其他约定</h2>
    <ol>
      <li>
        本协议的订立、效力、解释、履行及争议解决适用中华人民共和国法律。如果本协议的任何内容与法律相抵触，应以法律规定为准。
      </li>
      <li>就本协议产生的争议及纠纷，用户与拼拼购平台应友好协商解决。协商不成的，任何一方有权向本协议签订地（济南市历下区）人民法院提起诉讼。</li>
      <li>本协议于用户完成用户账号注册之日在济南市历下区签订并生效。即便用户没有完成用户账号注册，但通过其他任何方式获得和使用拼拼购平台所提供的产品和服务的，本协议视为在该用户获得及使用产品和服务之日签订并生效。</li>
    </ol>
  </div>
</template>
<script>
export default {
  props: ['servicesStatus']
}
</script>
<style scoped>
  /* 服务条款 */
.services{
  position: fixed;
  width: 90%;
  top: 0;
  margin: 5vh 5% 0 5%;
  height: 500px;
  height: 80vh;
  overflow-y: scroll;
  border-radius: 5px;
  background-color: #ffffff;
  z-index: 11;
}
.services h1{
  text-align: center;
  margin: 10px 0;
  font-weight: 800;
  font-size: 18px;
}
.services p{
  margin: 10px;
  text-align: left;
  line-height: 16px;
  text-indent: 30px;
  font-size: 14px;
}
.services h2{
  text-align: left;
  margin: 10px;
  font-weight: 600;
  font-size: 16px;
}
.services ol{
  list-style-type: decimal;
  font-size: 13px;
}
.services li{
  margin: 5px 0;
  text-align: left;
  line-height: 1.3em;
}
strong.red{
  color: red;
}
</style>



